import { useNow, useTimer } from '@speedlo/hooks'
import { differenceInMinutes } from 'date-fns'

import { GCompanyBranchStatus } from '../graph/types'

export type TStatus = GCompanyBranchStatus.Fragment

export type TStatusDecision = 'Open' | 'Closed' | 'ClosingSoon' | 'OpeningSoon'

export interface IShopStatusRender {
  decision: TStatusDecision
  openingIn: number
  closingIn: number
}

interface IProps {
  status: TStatus
  updateInterval?: number
  openSoonThresholdInMinutes?: number
  closeSoonThresholdInMinutes?: number
}

export function useShopStatus({
  status,
  updateInterval,
  openSoonThresholdInMinutes = 30,
  closeSoonThresholdInMinutes = 30,
}: IProps) {
  const getNow = useNow()
  useTimer({
    interval: updateInterval || 0,
    enabled: Boolean(updateInterval),
  })

  const now = getNow()
  const { isOpen, willCloseAt, willOpenAt } = status

  let decision: TStatusDecision

  if (isOpen) {
    decision = isSoon(willCloseAt, closeSoonThresholdInMinutes, now)
      ? 'ClosingSoon'
      : 'Open'
  } else {
    decision = isSoon(willOpenAt, openSoonThresholdInMinutes, now)
      ? 'OpeningSoon'
      : 'Closed'
  }

  return {
    decision,
    get openingIn() {
      return willOpenAt ? differenceInMinutes(willOpenAt, now) : 0
    },
    get closingIn() {
      return willCloseAt ? differenceInMinutes(willCloseAt, now) : 0
    },
  }
}

function isSoon(time: Maybe<string>, threshold: number, now: Date) {
  return time ? Math.abs(differenceInMinutes(time, now)) <= threshold : false
}
